import { Component, OnInit } from '@angular/core';
import { HttpService } from '../../api/http.service';
import { AlertController } from '@ionic/angular';
@Component({
  selector: 'app-offers',
  templateUrl: './offers.page.html',
  styleUrls: ['./offers.page.scss'],
  providers: [HttpService]
})
export class OffersPage implements OnInit {
  currentColor:any;
  response:any;
  userPoints:any;
  data:any;
  userId:any;
  responseValue:any;
  constructor(public alertController: AlertController,public shared: HttpService) { 
    this.currentColor = 'light';
  }

  ngOnInit() {
  }


  ionViewDidEnter() {
    
    if (navigator.onLine) {
            
            this.shared.ServiceCalloffers.ServiceCallRequestoffersData().then((res) => { 
              this.response=res;
              this.response=this.response.content.offers;
              var profile = localStorage.getItem('profile');
              this.data=JSON.parse(profile);
              this.userPoints= parseInt(this.data.content.profile.points);
              this.userId=this.data.content.profile.user_id
              
    
              }, err => {
            
               
              })
    
          }else{
            this.shared.displayNetworkUpdate('No Internet Connection')
            
    
          }

        }



        Redeem(items){

          this.presentAlertConfirm(items);
         
        }





        async presentAlertConfirm(items) {
          const alert = await this.alertController.create({
            header: '',
            message: 'Please redeem this offer only in front of the shopkeeper or ask the shopkeeper to redeem this offer for you',
            buttons: [
              {
                text: 'Cancel',
                role: 'cancel',
                cssClass: 'secondary',
                handler: (blah) => {
                  console.log('Confirm Cancel: blah');
                }
              }, {
                text: 'Redeem Now',
                handler: () => {
                  if (navigator.onLine) {
                    this.shared.ServiceCallRedeem.ServiceCallRequestRedeemCodeData(this.userId,items.id).then((res) => { 
                        this.responseValue=res;
                        if (this.responseValue.message=="success"){
                          localStorage.setItem('profile', JSON.stringify(this.responseValue));
                          this.shared.showAlert("Offer has been successfully redeemed","Congratulations")
                        }else{
                          this.shared.showAlert(this.responseValue.content,"Alert")
                        }
                        
                        }, err => {
                        })
              
                    }else{
                      this.shared.displayNetworkUpdate('No Internet Connection')
                    }
                }
              }
            ]
          });
      
          await alert.present();
        }

        Generate(items){
          if (navigator.onLine) {
          this.shared.ServiceCallGenerateCode.ServiceCallRequestGenerateCodeData(this.userId,items.id).then((res) => { 
              this.responseValue=res;
              this.shared.showAlert(this.responseValue.content,"Offer Redeem Code")
              }, err => {
              })
    
          }else{
            this.shared.displayNetworkUpdate('No Internet Connection')
            
    
          }
        }

}
